<style>
  .tab-wraper>.el-tabs>.el-tabs__header {
    margin: 0;
  }
  /*分页*/
  .pagination {
    padding: 20px 0;
    text-align: center;
    background-color: #fff;
  }
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <store-group @StoreGroupChange="StoreGroupChange"></store-group>

        <el-form-item label="门店类型">
          <el-select class="search-input" v-model="searchContent.saleType" placeholder="请选择门店类型"  size="small" @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in saleTypeOptList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="营业状态">
          <el-select class="search-input" v-model="searchContent.openStatus" placeholder="请选择营业状态"  size="small" @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in openStatusOptList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="启用状态">
          <el-select class="search-input" v-model="searchContent.openOnline" placeholder="请选择启用状态"  size="small" @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in openOnlineOptList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper tab-wraper" v-loading = "loading">
      <my-table ref="myTable" @openOnlineJobThirdP="openOnlineJobThirdP" @sureCategorical="sureCategorical" @openOnlineJob="openOnlineJob" @batchOpenHandel="batchOpenHandel"  @batchCloseHandel="batchCloseHandel" @batchEableHandel="batchEableHandel" @batchDisableHandel="batchDisableHandel" @batchEditCheck="batchEditCheck"></my-table>
    </div>

    <div class="pagination" v-show="tableData.length > 0">
      <el-pagination
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <div>
      <open-on-jtp ref="OpenOnlineJobThirdP" @doSearchHandel="doSearchHandel"></open-on-jtp>
    </div>

    <el-dialog title="批量切换捡货方式" :visible.sync="dialogVisible" width='460px'>
      <el-radio-group v-model="pickType">
        <el-radio :label="2">扫码完成捡货</el-radio>
        <el-radio :label="1">一键完成捡货</el-radio>
      </el-radio-group>
      <div class="mt20">
        <p>
          注意：1、扫码完成捡货，按实际重量减扣库存，实现差额退款；
        </p>
        <p style="text-indent: 3em;">
          2、当门店称重打码设备正常，应设置为扫码完成捡货方式；
        </p>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" size="mini" :disabled="isDisabled" @click="batchEditCheckHandel">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import * as utils from '@/utils/utils'
import MyTable from "../MyTable"
import OpenOnlineJobThirdP from "../OpenOnlineJobThirdP"
import Storage from '@/utils/localStorage'
import StoreGroupAndStore from '@/components/Common/StoreCommon/StoreGroupAndStore'
const storeMGTUrl = {
  // 商品管理-获取平台列表
  getPlatformList: '/plateform/a/product/getPlatformList',
  // 门店管理界面，获取门店按钮接口
  aquire: '/plateform/a/store/erp/aquire',
  // 门店管理-搜索结果列表
  show: '/plateform/a/store/platform/show',
  // 关联商品分类
  newStore: '/plateform/a/store/platform/newStore',
  // 【门店业务】线上业务和门店营业
  batchUpdateStatus: '/plateform/a/store/platform/updateStatus',
  // 批量切换拣货方式
  editCheck: '/plateform/a/store/editCheck'
}
export default{
  data() {
    return {
      loading: false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      searchContent: {
        storeGroupTypeNo: '',
        storeGroupNo: '',
        storeNo: '', // 门店编号
        openStatus: '', // 营业状态 0-全部；1-营业中；2-歇业中；
        openOnline: '', // 开启线上业务：0：未开启；1：已开启；
        saleType: '', // ERP门店经营类型：1-直营；2-加盟店
        platform: 5 // 1-有赞；2-京东到家；3-美团外卖；4-饿了么 5-本来精选
      },
      // 营业状态下拉框列表
      openStatusOptList: [
        {
          label: '全部',
          value: ''
        },
        {
          label: '营业中',
          value: 1
        },
        {
          label: '歇业中',
          value: 2
        },
        {
          label: '待开业',
          value: 0
        }
      ],
      // 门店类型
      saleTypeOptList: [
        {
          label: '全部',
          value: ''
        },
        {
          label: '直营',
          value: 1
        },
        {
          label: '加盟',
          value: 2
        },
        {
          label: '分销',
          value: 3
        },
        {
          label: '自提',
          value: 4
        }
      ],
      // 启用状态
      openOnlineOptList: [
        {
          label: '全部',
          value: ''
        },
        {
          label: '禁用',
          value: 0
        },
        {
          label: '启用',
          value: 1
        }
      ],
      // 上线渠道下拉框列表
      unOpenedPlatformOptList: [
        {
          "name": "全部",
          "platformId": ""
        }
      ],
      tableData: [],

      // 切换批量拣货方式 选中的门店的id
      ids: [],
      dialogVisible: false,
      isDisabled: false,
      pickType: null
    }
  },
  beforeMount() {
    let searchContentBlxMgt = Storage.get("searchContentBlxMgt")
    if(this.$route.meta.keepAlive && searchContentBlxMgt) {
      this.searchContent = searchContentBlxMgt.searchContent
    }
  },
  mounted() {
    // 根据用户选择的城市获取数据
    this.doSearchHandel(true)
  },
  components: {
    "open-on-jtp": OpenOnlineJobThirdP,
    "my-table": MyTable,
    "store-group": StoreGroupAndStore
  },
  methods: {
    StoreGroupChange(params) {
      this.searchContent.storeNo = params.storeNo
      this.searchContent.storeGroupTypeNo = params.storeGroupTypeNoList[0]
      this.searchContent.storeGroupNo = params.storeGroupTypeNoList[1]
      this.doSearchHandel(true)
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel(_param) {
      if(_param === true) {
        this.pageNum = 1
      }
      this.loading = true
      this.$http.post(storeMGTUrl.show, {...this.searchContent, pageSize: this.pageSize, pageNum: this.pageNum}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data) {
            if(res.data.list && res.data.list.length) {
              for(let item of res.data.list) {
                item.uniqueId = item.storeId + item.storeNo
              }
              this.tableData = res.data.list
              let _value = {
                platformCode: this.searchContent.platform,
                tableData: this.tableData
              }
              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
            }else {
              this.tableData = []
              let _value = {
                platformCode: this.searchContent.platform,
                tableData: this.tableData
              }

              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
            }

            if(this.pageNum === 1) {
              this.total = res.data.total
            }
          }else {
            this.tableData = []
            let _value = {
              platformCode: this.searchContent.platform,
              tableData: this.tableData
            }
            if(this.$refs.myTable) {
              this.$refs.myTable.setDefaultMesg(_value)
            }
          }
          
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [changeSize 分页]
     * @param  {[type]} _val [description]
     * @return {[type]}      [description]
     */
    changeSize(value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    changeCurrent(value) {
      this.pageNum = value
      this.doSearchHandel()
    },
    /**
     * [openOnlineJob 本来鲜编辑]
     * @return {[type]} [description]
     */
    openOnlineJob(_val) {
      // 设置搜索条件
      let _paramMesg = {
        searchContent: this.searchContent
      }
      Storage.set("searchContentBlxMgt", _paramMesg)

      let platformId = _val.platformId
      let storeNo = _val.storeNo
      let storeId = _val.storeId
      this.$router.push("/blx_store/o_ol_job/" + `${platformId}/${storeNo}/${storeId}`)
    },
    /**
     * [unPlatform 三方平台开启线上业务]
     * @param  {[type]} _val [description]
     * @return {[type]}      [description]
     */
    openOnlineJobThirdP(_val) {
      if(_val) {
        this.$refs.OpenOnlineJobThirdP.setDefaule(_val)
      }
    },
    /**
     * [sureCategorical 关联商品分类]
     * @return {[type]} [description]
     */
    sureCategorical(val){
      if(val.storeNo) {
        let storeNo = val.storeNo
        this.$http.get(storeMGTUrl.newStore, {storeNo: storeNo}).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    },
    /**
     * [batchOpenHandel 批量营业]
     * @param  {[type]} _value [description]
     * @return {[type]}        [description]
     */
    batchOpenHandel(_value) {
      let checkedListArr = _value.checkedList
      let platformStoreIds = []
      for(let item of checkedListArr) {
        if(item.openStatus === 0) {
          this.$notify.warning({
            title: '温馨提示',
            // message: `(${item.storeNo}-${item.storeName})_还没有开业，不能直接操作营业/歇业`
            message: '勾选的门店有待开业的，不能直接批量操作营业/歇业'
          })
          return
        }
        platformStoreIds.push(item.platformStoreId)
      }
      
      utils.showLoading()
      this.$http.post(storeMGTUrl.batchUpdateStatus, {'ids': platformStoreIds, 'openStatus': 1}).then(res => {
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("批量营业成功")
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
      
    },
    /**
     * [batchCloseHandel 批量歇业]
     * @param  {[type]} _value [description]
     * @return {[type]}        [description]
     */
    batchCloseHandel(_value) {
      let checkedListArr = _value.checkedList
      let platformStoreIds = []
      for(let item of checkedListArr) {
        if(item.openStatus === 0) {
          this.$notify.warning({
            title: '温馨提示',
            // message: `(${item.storeNo}-${item.storeName})_还没有开业，不能直接操作营业/歇业`
            message: '勾选的门店有待开业的，不能直接批量操作营业/歇业'
          })
          return
        }
        platformStoreIds.push(item.platformStoreId)
      }

      utils.showLoading()
      this.$http.post(storeMGTUrl.batchUpdateStatus, {'ids': platformStoreIds, 'openStatus': 2}).then(res => {
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("批量歇业成功")
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })

    },
    /**
     * [batchEableHandel 批量启用]
     * @param  {[type]} _value [description]
     * @return {[type]}        [description]
     */
    batchEableHandel(_value) {
      let mobile = /^(\d{3,4}-)\d{7,8}$/
      let phone = /^[1][2,3,4,5,6,7,8][0-9]{9}$/

      let checkedListArr = _value.checkedList
      let platformStoreIds = []
      let storeIds = []
      for(let item of checkedListArr) {
        if(item.shopId != 0 && !item.shopId && item.platformId !== 5) {
          this.$notify.warning({
            title: '温馨提示',
            message: '勾选的门店存在三方门店ID为空的门店，不能直接操作启用/禁用'
          })
          return
        }

        if(!item.phone) {
          this.$notify.warning({
            title: '温馨提示',
            message: `【${item.storeNo}_${item.storeName}】的门店电话不能为空`
          })
          return
        }
        storeIds.push(item.storeId)
        platformStoreIds.push(item.platformStoreId)
      }
      let params = {
        
      }
      let pickupStartTime = _value.pickupStartTime || ''
      let pickupEndTime = _value.pickupEndTime || ''
      utils.showLoading()
      this.$http.post(storeMGTUrl.batchUpdateStatus, {ids: platformStoreIds, storeIds: storeIds, openOnline: 1, pickupStartTime: pickupStartTime, pickupEndTime: pickupEndTime}).then(res => {
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("批量启用成功")
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [batchDisableHandel 批量禁用]
     * @param  {[type]} _value [description]
     * @return {[type]}        [description]
     */
    batchDisableHandel(_value) {
      let checkedListArr = _value.checkedList
      let platformStoreIds = []
      let storeIds = []
      for(let item of checkedListArr) {
        if(item.shopId != 0 && !item.shopId && item.platformId !== 5) {
          this.$notify.warning({
            title: '温馨提示',
            message: '勾选的门店存在三方门店ID为空的门店，不能直接操作启用/禁用'
          })
          return
        }
        storeIds.push(item.storeId)
        platformStoreIds.push(item.platformStoreId)
      }

      utils.showLoading()
      this.$http.post(storeMGTUrl.batchUpdateStatus, {'ids': platformStoreIds, 'storeIds': storeIds, 'openOnline': 0}).then(res => {
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("批量禁用成功")
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [batchEditCheck 批量切换拣货方式选择弹窗]
     * @param  {[type]} _value [description]
     * @return {[type]}        [description]
     */
    batchEditCheck(_value) {
      let checkedListArr = _value.checkedList
      let ids = []
      for(let item of checkedListArr) {
        ids.push(item.platformStoreId)
      }
      if(checkedListArr.length === 1) {
        this.pickType = checkedListArr[0].pickType
      }else {
        this.pickType = null
      }
      
      this.ids = ids
      this.dialogVisible = true
      
    },
    /**
     * [batchEditCheckHandel 批量切换拣货方式]
     * @return {[type]} [description]
     */
    batchEditCheckHandel() {
      this.disabled = true
      this.$http.post(storeMGTUrl.editCheck, {'ids': this.ids, 'pickType': this.pickType}).then(res => {
        setTimeout(() => {
          this.disabled = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("批量切换捡货方式成功")
          this.dialogVisible = false
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        setTimeout(() => {
          this.disabled = false
        }, 800)
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    }

  },
  /**
   * [beforeRouteLeave 页面离开]
   * @param  {[type]}   to   [description]
   * @param  {[type]}   from [description]
   * @param  {Function} next [description]
   * @return {[type]}        [description]
   */
  beforeRouteLeave (to, from, next){
    from.meta.keepAlive = false;
    next();
  }

}
</script>



